@import url(https://fonts.googleapis.com/css2?family=MuseoModerno:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  background-color: #122333;
  font-family: 'MuseoModerno', cursive;
  overflow: hidden;
}

h1{
  margin: 0;
}

.container{
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.miniContainer{
  display: flex;
  flex-direction: column;
  height: 650px;
  width: 500px;
  justify-content: center;
  align-items: center;
  background-color: #182B3C;
  box-shadow: 15px 15px 15px rgba(0, 0, 0, 0.301);
  border-radius: 20px;
  margin-top: 50px;
}

.display{
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 50px;
  width: 450px;
  background-color: #21374A;
  border-radius: 10px;
  margin-top: 30px;
  box-shadow: inset 3px 3px 3px #131e29, 
    inset -3px -3px 3px #274057;
}

.start{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  width: 200px;
  margin-top: 20px;
  border-style: solid;
  border-width: 2px;
  border-radius: 50px;
  border-color: #2EAC68;
  font-weight: bold;
}

.start, .btn:hover{
  cursor: pointer;
}

.start:hover{
  background-color: #2EAC68;
  transition: all .4s ease;
}

@media only screen and (max-width: 600px){
  .miniContainer{
    width: 340px;
  }
  .display{
    width: 300px;
  }
  h1{
    font-size: 1.3rem;
  }
}


.button-container{
    
    width: 450px;
    height: 100px;
    margin-top: 20px;
    
    border-radius: 10px;
}

.counter{
    margin: 10px 0 5px 0;
    font-weight: bold;
}

.slider-container{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    background-color: #21374A;
    width: 100%;
    height: 50px;
    border-radius: 10px;
    box-shadow: inset 3px 3px 3px #131e29, 
    inset -3px -3px 3px #274057;
}

.slider{
    -webkit-appearance: none;
    width: 400px;
    height: 2px;
    background-color: #2EAC68; 
}

.slider::-webkit-slider-thumb{
    cursor: pointer;
    -webkit-appearance: none;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: #122333;
}


@media only screen and (max-width: 600px){
    .button-container{
      width: 300px;
    }
    .slider{
        width: 250px;
    }
  }
  
.btnBox{
    width: 450px;
}

.optBtn{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    background-color: #21374A;
    border-radius: 10px;
    height: 50px;
    margin-top: 10px;
    padding: 0 20px;
    box-shadow: inset 3px 3px 3px #131e29, 
    inset -3px -3px 3px #274057;
  }
  
  .btn{
    display: flex;
    align-items: center;
    height: 24px;
    width: 50px;
    border-radius: 50px;
    position: relative;
  }

  .btnTop, .btnTop2, .btnTop3, .btnTop4{
    position: absolute;
    height: 20px;
    width: 20px;
    background-color: #122333;
    border-radius: 50%;
    transition: all .3s ease;
    -webkit-transform: translateX(26px);
            transform: translateX(26px);
    
  }

  .none{
    height: 24px;
    width: 50px;
    border-radius: 50px;
    position: absolute;
    background-color: #2EAC68;
    transition: all .4s ease;
  }
  
  .btn-base{
    height: 24px;
    width: 50px;
    background-color: #182B3C;
    border-radius: 50px;
    transition: all .4s ease;
  }
  
  .btnTop.off{
    transition: all .3s ease;
    -webkit-transform: translateX(4px);
            transform: translateX(4px);
  }
  
  .btnTop2.off{
    transition: all .3s ease;
    -webkit-transform: translateX(4px);
            transform: translateX(4px);
  }
  .btnTop3.off{
    transition: all .3s ease;
    -webkit-transform: translateX(4px);
            transform: translateX(4px);
  }
  .btnTop4.off{
    transition: all .3s ease;
    -webkit-transform: translateX(4px);
            transform: translateX(4px);
  }


@media only screen and (max-width: 600px){
  .btnBox{
    width: 300px;
  }
  h2{
    font-size: 1.3rem;
  }
}

